@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
  }
  
  /* centering a div using flex-box */
  .home-parent{
      width: 100vw;
      height: 100vh;
      background-color:rgb(39, 51, 87);
      display: -webkit-flex;
      display: flex;
      -webkit-justify-content: center;
              justify-content: center;
      -webkit-align-items: center;
              align-items: center;
  }
  
  .home-cont{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}
  
.home-cont h1{
    color: white;
    font-size: 3.8rem;
    font-family: sans-serif;
    font-weight: 100;
    letter-spacing: 1px;
  }
  
  .home-cont button{
    width: 260px;
    height: 50px;
    border: none;
    border-radius: 20px;
    background-color: green;
    font-size: 25px;
    font-family: sans-serif;
    color: aliceblue;
    margin-top: 18px;
    padding: 7px;
    outline: none;
    cursor: pointer;
  }
  
  .home-cont button:hover{
      color: white;
    background-color: darkgreen;
    transition: -webkit-transform .2s;
    transition: transform .2s;
    transition: transform .2s, -webkit-transform .2s;
    font-size: 30px;
  }
  
*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.quiz-parent {
  width: 100vw;
  height: 100vh;
  background-color: rgb(39, 51, 87);
  position: relative;
}

.quiz-cont {
  width: 70vw;
  height: 60vh;
  background-color: azure;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  display: grid;
  place-items: center;
}

.head {
  padding-top: 30px;
  display: grid;
  place-items: center;
  font-size: 35px;
  font-family: serif;
  font-weight: 100;
}

#numbers {
  position: relative;
  right: 90px;
  float: left;
  color: black;
  font-size: 20px;
  font-weight: 300;
  letter-spacing: normal;
  font-family: serif;
}

#question {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  padding-top: 20px;
  font-size: 20px;
  font-family: 'Roboto', sans-serif;
}

.options-container {
  display: grid;
  place-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  width: 650px;
  height: 200px;
}

.option-cont-1 {
  margin-bottom: -70px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  text-align: center;
}
.option-cont-2 {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  text-align: center;
}

.options-container button {
  width: 350px;
  height: 40px;
  margin: 10px;
  outline: none;
  border: none;
  border-radius: 30px;
  background-color: rgb(30, 144, 255);
  color: white;
  font-size: 15px;
  font-family: sans-serif;
  cursor: pointer;
}

.controls {
  padding-bottom: 50px;
}

.controls #prev {
  margin-right: 25px;
  width: 90px;
  height: 30px;
  background-color: rgb(30, 144, 255);
  color: white;
  font-size: 15px;
  font-family: sans-serif;
  border: none;
  border-radius: 2px;
  outline: none;
  cursor: pointer;
}

.controls #next {
  margin-right: 25px;
  width: 70px;
  height: 30px;
  background-color: green;
  color: white;
  font-size: 15px;
  font-family: sans-serif;
  border: none;
  border-radius: 2px;
  outline: none;
  cursor: pointer;
}

.controls #quit {
  margin-right: 25px;
  width: 70px;
  height: 30px;
  background-color: red;
  color: white;
  font-size: 15px;
  font-family: sans-serif;
  border: none;
  border-radius: 2px;
  outline: none;
  cursor: pointer;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.result-parent {
  width: 100vw;
  height: 100vh;
  background-color: rgb(39, 51, 87);
  margin: auto;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.result-cont {
  width: 40vw;
  height: 45vh;
  background-color: azure;
  display: grid;
  place-items: center;
}

.result {
  margin-top: -120px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
.result i {
  color: green;
  font-size: 50px;
  font-weight: 500;
}

.result h1 {
  color: green;
  font-size: 40px;
  font-weight: 200;
  font-family: sans-serif;
  margin-bottom: 15px;
}
.status {
  font-size: 25px;
  padding-top: 30px;
}

.score {
  font-size: 40px;
  color: dodgerBlue;
  font-family: "Roboto", sans-serif;
  padding-top: 20px;
  padding-bottom: 15px;
}

.main {
  margin-bottom: 40px;
}

.main p {
  font-size: 18px;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  color: gray;
}

.btn-container {
  margin-top: 20px;
}

.btn-container .play {
  outline: none;
  width: 100px;
  height: 30px;
  padding: 5px;
  color: white;
  background-color: teal;
  font-size: 15px;
  font-family: sans-serif;
  border: 2px solid teal;
  border-radius: 5px;
  margin-right: 30px;
}

.btn-container .play:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.btn-container .back {
  width: 110px;
  height: 30px;
  padding: 5px;
  color: whitesmoke;
  background-color: green;
  font-size: 15px;
  font-family: sans-serif;
  border: 2px solid green;
  outline: none;
  border-radius: 5px;
}

.btn-container .back:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}


